import React, { useState } from 'react';
import './calcul.css';

const App = () => {
  const [input, setInput] = useState('');
  const [result, setResult] = useState('');

  const handleClick = (value) => {
    if (value === '=') {
      try {
        setResult(eval(input).toString());
      } catch (error) {
        setResult('Error');
      }
    } else if (value === 'C') {
      setInput('');
      setResult('');
    } else {
      setInput(input + value);
    }
  };

  return (
    <div className="calculator">
      <div className="display">
        <h1>Calculatrice</h1>
        <input type="text" value={input} readOnly />
        <div className="result">{result}</div>
      </div>
      <div className="buttons">
        <button onClick={() => handleClick('7')} className='bb'>7</button>
        <button onClick={() => handleClick('8')} className='bb'>8</button>
        <button onClick={() => handleClick('9')} className='bb'>9</button>
        <button onClick={() => handleClick('/')} className='bb'>/</button>

        <button onClick={() => handleClick('4')} className='bb'>4</button>
        <button onClick={() => handleClick('5')}className='bb'>5</button>
        <button onClick={() => handleClick('6')}className='bb'>6</button>
        <button onClick={() => handleClick('*')}className='bb'>*</button>

        <button onClick={() => handleClick('1')}className='bb'>1</button>
        <button onClick={() => handleClick('2')}className='bb'>2</button>
        <button onClick={() => handleClick('3')}className='bb'>3</button>
        <button onClick={() => handleClick('-')}className='bb'>-</button>

        <button onClick={() => handleClick('0')}className='bb'>0</button>
        <button onClick={() => handleClick('.')}className='bb'>.</button>
        <button onClick={() => handleClick('C')}style={{background:'#ff6347n'}}className='bb'>
          C
        </button>

        <button onClick={() => handleClick('+')}className='bb'>+</button>

              <button onClick={() => handleClick('=')}  className="clear" style={{height:"30px",justifyContent:"center",marginLeft:"15px"}}>=</button>
      </div>
    </div>
  );
};

export default App;
